:root {
  font-family: Inter, Avenir, Helvetica, Arial, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
}

.link {
  color: #fff;
  width: 100px;
  text-align: center;
  background-color: #00f;
  border-radius: 5px;
  margin: 20px 0 0 20px;
  padding: 7px 12px;
  text-decoration: none;
  display: inline-block;
}

.filter-form {
  margin-top: 30px;
}

.form-conteiner {
  justify-content: center;
  display: flex;
}

label {
  flex-direction: column;
  align-items: center;
  margin-left: 10px;
  font-size: 20px;
  display: flex;
}

select {
  cursor: pointer;
  border: 1px solid tomato;
  border-radius: 5px;
  margin-top: 10px;
  padding: 5px 2px;
  font-size: 15px;
}

div {
  justify-content: center;
  margin-top: 10px;
  display: flex;
}

button {
  cursor: pointer;
  border: 1px solid #9acd32;
  border-radius: 7px;
  margin-left: 10px;
  padding: 5px 10px;
}

button:hover {
  color: #fff;
  background-color: #00f;
}

